<template>
<div class="search">
    <Head :agood='1' :bgood='bgood' :cgood='3'></Head>
    <div class="content">
        <div class="happy">
            <input type="text" placeholder="11111111111111111111111"/>
            <input type="button" value="提交"/>
        </div>
    </div>
    <bottom></bottom>
</div>    
</template>
<script>
import bottom from '@/components/bottom'
import Head from '@/components/head/Head'
export default {
    name:'search',
    components:{Head,bottom},
    data(){
        return{
            bgood:'搜索',
        }
    }
}
</script>
<style lang='scss' scoped>
.content{
    margin-top: .91rem;
    width: 100%;
    height: 100%;
    background: white;
    .happy{
        width: 95%;
        height: 1.2rem;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;
        input{
            &:nth-of-type(1){
                font-size: .3rem;
                width: 80%;
                height: .6rem;
                background: #f5f5f5;
                border: none;
                border-radius: 0.05rem;
            }
            &:nth-of-type(2){
                width: 16%;
                height: .6rem;
                margin-left: .2rem;
                background: #3190e8;
                color: white;
                border: none;
                border-radius: 0.05rem;
                font-size: .3rem;
                font-weight: bold;
            }
        }
    //     input::-webkit-input-placeholder{
    //   	color: rgb(117, 122, 121);
    //       font-size: .3rem;
    //       font-weight:bold; 
    //   }
    }
}

</style>



